import React, { Component } from "react";
import Link from "next/link";
export default class Navigation extends Component {
  constructor() {
    super();
    this.state = {
      sticky: false,
    };
  }
  componentDidMount() {
    window.addEventListener("scroll", this.handleScroll);

    //Mobile Menu
    this.mobileMenu();
  }

  componentWillUnmount() {
    window.removeEventListener("scroll", this.handleScroll);
  }

  handleScroll = () => {
    if (window.scrollY > 70) {
      this.setState({
        sticky: true,
      });
    } else if (window.scrollY < 70) {
      this.setState({
        sticky: false,
      });
    }
  };

  mobileMenu = () => {
    //Mobile Menu Toggle
    let mainNavToggler = document.querySelector(".menu-toggler");
    let mainNav = document.querySelector(".main-navigation");
    let scrollToLink = document.querySelectorAll(".scrollToLink");
    mainNavToggler.addEventListener("click", function () {
      mainNav.style.display =
        mainNav.style.display != "block" ? "block" : "none";
    });
    scrollToLink.forEach((link) => {
      link.addEventListener("click", function () {
        mainNav.style.display = "none";
      });
    })
  };

  

  render() {
    return (
      <header
        className="site-header site-header__header-one"
        style={{ "z-index": "10200" }}
      >
        <nav
          className={`navbar navbar-expand-lg navbar-light header-navigation stricky ${
            this.state.sticky ? "stricked-menu stricky-fixed" : ""
          }`}
        >
          <div className="container clearfix">
            <div className="logo-box clearfix">
              <a className="navbar-brand" href="/">
                <img
                  src="/assets/images/resources/logo.png"
                  className="main-logo"
                  width="50"
                  alt="Awesome Image"
                />
              </a>
              <button className="menu-toggler">
                <span className="fa fa-bars"></span>
              </button>
            </div>
            <div className="main-navigation">
              <ul className=" one-page-scroll-menu navigation-box">
                <li className="scrollToLink">
                  <Link href="/">
                    <a>Home</a>
                  </Link>
                </li>
                <li className="scrollToLink">
                  <Link href="#event">
                    <a>Acara</a>
                  </Link>
                </li>
                <li className="scrollToLink">
                  <Link href="#timeline">
                    <a>Timeline</a>
                  </Link>
                </li>
                <li className="scrollToLink">
                  <Link href="#faq">
                    <a>FAQ</a>
                  </Link>
                </li>
                <li className="scrollToLink">
                  <Link href="#sponsor">
                    <a>Sponsor</a>
                  </Link>
                </li>
                {/* make login button */}
                <li className="scrollToLink">
                  <Link href="https://facebook.com">
                    <a className="btn btn-primary">Login</a>
                  </Link>
                </li>   
              </ul>
            </div>
          </div>
        </nav>
      </header>
    );
  }
}
