import React,{useEffect,useState} from "react";
import { baseURL } from "../config/config";

const MediaPartner = () => {
    const [dataMP,setDataMP] = useState([])
    useEffect(()=>{
        const url = baseURL+"api/medpart"
        fetch(url).then((response)=>{
            response.json().then((data)=>{
                setDataMP(data)
            })
        })        
    },[])
  return (
    <div className="brand-one my-5 py-5">
      <div className="container">
        <div className="block-title text-center">
          <h2 className="block-title__title">
            <span> Media Partner </span>{" "}
          </h2>{" "}
        </div>{" "}
        <div className="row justify-content-center medpart">
          {" "}
          {dataMP.map((item, index) => {
            return (
              <div key={index} className="item  col-md-2 col-3 text-center d-flex align-items-center ">
                <img
                 
                  className="img-fluid w-100  my-3"
                  src={baseURL+`uploads/media/`+item.gambarMed}
                  alt=""
                />
              </div>
            );
          })}{" "}
        </div>{" "}
      </div>{" "}
    </div>
  );
};
export default MediaPartner;
